Office.onReady((info) => {
    if (info.host === Office.HostType.Excel) {
        document.getElementById("sideload-msg").style.display = "none";
    }
});

function startMonitoring() {
    Excel.run(async (context) => {
        const sheet = context.workbook.worksheets.getActiveWorksheet();
        sheet.onChanged.add(onSheetChange);
        await context.sync();
        alert("Auto-calculation started! Enter dimensions in any cell.");
    }).catch((error) => {
        console.error(error);
        alert("Error: " + error.message);
    });
}

async function onSheetChange(args) {
    await Excel.run(async (context) => {
        const sheet = context.workbook.worksheets.getActiveWorksheet();
        const changedRange = sheet.getRange(args.address);
        changedRange.load("values, address, columnIndex");
        await context.sync();

        const dimension = changedRange.values[0][0];
        if (!dimension) return;

        const regex = /(\d+)'(\d+)?"?\s*x\s*(\d+)'(\d+)?"?/i;
        const match = dimension.match(regex);
        if (!match) return;

        const lengthFeet = parseInt(match[1]) + (parseInt(match[2]) / 12);
        const heightFeet = parseInt(match[3]) + (parseInt(match[4]) / 12);
        const squareFeet = lengthFeet * heightFeet;

        const row = parseInt(args.address.match(/\d+/)[0]);
        const rightColumnIndex = changedRange.columnIndex + 1;
        const rightCellAddress = String.fromCharCode(65 + rightColumnIndex) + row;
        const resultCell = sheet.getRange(rightCellAddress);
        resultCell.values = [[squareFeet.toFixed(2)]];
        await context.sync();
    }).catch((error) => {
        console.error(error);
        alert("Error: " + error.message);
    });
}